lement {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n\n  .MuiTypography-root {\n    display: inline-block;\n    font-size: 1rem;\n    padding: 6px 0;\n    background-image: linear-gradient($tealAccent 0 0);\n    background-position: 0 100%;\n    background-size: 0% 3px;\n    background-repeat: no-repeat;\n  }\n\n  .MuiTypography-root,\n  a {\n    transition: color 0.2s, background-size 0.3s, background-position 0s 0.3s;\n  }\n\n  a {\n    color: whitesmoke;\n  }\n\n  &:hover {\n    .MuiTypography-root,\n    a {\n      background-position: 100% 100%;\n      background-size: 100% 3px;\n    }\n  }\n}\n",".components-body {\n  width: 60%;\n}\n\n#no-islands {\n  margin: 10px;\n  font-size: 1rem;\n}\n\n#panel-container {\n  // set height of panel to all available vertical space minus the height of the header\n  height: calc(100vh - 64px);\n  background-color: $softBlack;\n  width: 59%;\n  overflow-x: none;\n  overflow-y: auto;\n  border-right: 1px solid #3d424a;\n\n  #panel-header {\n    display: flex;\n    background-color: $softerBlack;\n    flex-direction: column;\n    justify-content: flex-start;\n    align-items: stretch;\n\n    #panel-toggle {\n      display: flex;\n      justify-content: center;\n      border-bottom: 1px solid #3d424a;\n      padding: 8px;\n      .buttonToggle {\n        font-family: 'Figtree', sans-serif;\n        background-color: $softerBlack;\n        opacity: 0.5;\n        color: whitesmoke;\n        padding: 5px 15px;\n        border-color: rgb(211, 211, 211);\n        border: 1px solid grey;\n        text-transform: uppercase;\n        box-shadow: 1px 1px grey;\n        transition: ease background-color 250ms;\n        &:hover {\n          background-color: #d494ff63;\n        }\n        &:disabled {\n          cursor: default;\n          opacity: 0.7;\n        }\n        &.active {\n          opacity: 1;\n          background-color: #72329d;\n        }\n      }\n      .buttonToggle.button0 {\n        border-radius: 5px 0px 0px 5px;\n      }\n      .buttonToggle.button1 {\n        border-radius: 0px 5px 5px 0px;\n      }\n    }\n\n    #search-bar {\n      display: flex;\n      align-items: center;\n      padding: 2px 10px;\n      .search-icon {\n        opacity: 0.5;\n        scale: 0.8;\n      }\n      #text-to-search {\n        background-color: $softerBlack;\n        width: 100%;\n        border: none;\n        margin: 5px;\n        padding-left: 10px;\n      }\n    }\n  }\n  .MuiTreeItem-label {\n    font-family: 'Figtree', sans-serif;\n  }\n  .MuiTreeItem-content {\n    :hover {\n      background-color: #d494ff21;\n    }\n    &.Mui-selected {\n      background-color: #8c4ab821;\n    }\n  }\n\n  .element,\n  .component {\n    .MuiTreeView-root {\n      // set the tree display height to the full available space minus the search bar and header\n      height: calc(100vh - 160px);\n\n      // to avoid vertical scrolling due to list items taking up too much space\n      li {\n        width: 95%;\n      }\n    }\n  }\n}\n\n@media (max-width: $panel-breakpoint) {\n  #panel-container {\n    width: 100%;\n    height: calc(50vh - 49px);\n    overflow: hidden;\n\n    .element,\n    .component {\n      .MuiTreeView-root {\n        height: calc(50vh - 160px);\n      }\n    }\n  }\n}\n\n::placeholder {\n  /* Chrome, Firefox, Opera, Safari 10.1+ */\n  color: #f8f8ff;\n  opacity: 0.5; /* Firefox */\n}\n","#sidepane-container {\n  // i don't know why this works but it avoids weird scroll behavior; the 105px is a necessary and exact value\n  height: calc(100vh - 105px);\n  overflow-y: auto;\n\n  width: 39%;\n  background-color: $softBlack;\n  padding: 20px;\n  color: whitesmoke;\n  margin: 0;\n  font-size: 1rem;\n\n  .MuiTreeView-root {\n    height: auto;\n\n    // to avoid horizontal scrolling due to list items taking up too much space\n    li {\n      width: 33vw;\n\n      ul li {\n        width: 30vw;\n      }\n    }\n  }\n}\n\nhr {\n  margin: 30px -20px 30px;\n  border: 0;\n  border-top: 1px solid #3d424a;\n}\n\nh3 {\n  color: $tealAccent;\n  // text-shadow: 0 0 0.5px gray;\n}\n\n@media (max-width: $panel-breakpoint) {\n  #sidepane-container {\n    width: 100%;\n    height: calc(50vh - 49px);\n    border-top: 3px solid #3d424a;\n  }\n}\n",".search-bar-text {\n  font-size: 14px;\n  color: whitesmoke;\n  &:focus {\n    outline: none;\n  }\n}\n\n.prev-next {\n  display: flex;\n  align-items: center;\n\n  .MuiSvgIcon-root {\n    &:active {\n      color: $tealAccent;\n    }\n\n    &:hover {\n      cursor: pointer;\n    }\n  }\n}\n\n.separator {\n  height: 20px;\n  width: 1px;\n  flex: 0 0 1px;\n  margin: 0 0.25rem;\n  background-color: #3d424a;\n}\n\n#search-bar {\n  .expand-collapse {\n    min-width: auto;\n    color: lightgrey;\n  }\n}\n.MuiButton-root.expand-collapse:hover {\n  -webkit-text-decoration: none;\n  text-decoration: none;\n  background-color: #6befe087;\n}\n"],"sourceRoot":""}]);
